/* Navbar Container */
.nav-container {
    width: 100%;
    background-color: #ffffff;
    padding: 15px 40px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    position: sticky;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Logo or Brand Placeholder */
.nav-logo {
    font-size: 24px;
    font-weight: 700;
    color: #36a9c0;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Hamburger Menu */
.hamburger-menu {
    display: none;
    background: none;
    border: none;
    font-size: 24px;
    color: #36a9c0;
    cursor: pointer;
    padding: 5px;
    transition: all 0.3s ease;
}

.hamburger-menu:hover {
    color: #24bcde;
}

/* Nav Links Wrapper */
.navlink {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 40px;
}

/* Individual NavLinks */
.navlink a {
    text-decoration: none;
    color: #36a9c0;
    font-weight: 600;
    font-size: 16px;
    padding: 8px 12px;
    border-radius: 6px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.navlink a.active {
    color: white;
    background-color: #36a9c0;
}

.navlink a:hover {
    background-color: #24bcde;
    color: white;
}

/* Responsive Styling */
@media (max-width: 768px) {
    .nav-container {
        padding: 15px 20px;
    }

    .hamburger-menu {
        display: block;
    }

    .navlink {
        position: fixed;
        top: 70px;
        left: 0;
        right: 0;
        background: white;
        flex-direction: column;
        gap: 0;
        padding: 20px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        transform: translateY(-100%);
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease;
    }

    .navlink.active {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
    }

    .navlink a {
        width: 100%;
        text-align: center;
        padding: 15px;
        border-radius: 0;
        border-bottom: 1px solid #f0f0f0;
    }

    .navlink a:last-child {
        border-bottom: none;
    }

    .navlink a:hover {
        background-color: #f8f9fa;
    }

    .navlink a.active {
        background-color: #36a9c0;
        color: white;
    }
}
  